<template>
  <div>
    <el-form :inline="true" class="demo-ruleForm">
      <el-form-item label="服务名" prop="service">
        <el-input v-model="service" placeholder="请输入服务名"></el-input>
      </el-form-item>
      <el-form-item label="方法名" prop="method">
        <el-input v-model="method" placeholder="请输入方法名"></el-input>
      </el-form-item>
      <el-form-item prop="startTime">
        <el-date-picker
          v-model="startTime"
          type="date"
          placeholder="开始时间"
        ></el-date-picker>
      </el-form-item>
      <el-form-item prop="endTime">
        <el-date-picker
          v-model="endTime"
          type="date"
          placeholder="结束时间"
        ></el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="search">查询</el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="info" @click="clear">重置</el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="logExport">导出</el-button>
      </el-form-item>
    </el-form>
    <div>
      <el-table :data="tableData" style="width: 100%;">
        <el-table-column type="index" label="序号" width="80"></el-table-column>
        <el-table-column prop="system" label="系统名称"></el-table-column>
        <el-table-column prop="type" label="请求方法"></el-table-column>
        <el-table-column
          prop="service"
          label="服务名"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="method"
          label="方法名"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="request"
          label="请求报文"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column
          prop="response"
          label="返回报文"
          show-overflow-tooltip
        ></el-table-column>
        <el-table-column prop="feeTime" label="耗费时间"></el-table-column>
        <el-table-column prop="opTime" label="配置时间"></el-table-column>
      </el-table>
    </div>
    <div class="footer">
      <el-row>
        <div class="block">
          <span class="demonstration"></span>
          <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            :page-sizes="pageSizes"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="rowCount"
          ></el-pagination>
        </div>
      </el-row>
    </div>
  </div>
</template>
<script>
import { postDownload } from "@/common/utils/tools";
export default {
  data() {
    return {
      params: {},
      tableData: [],
      pageSizes: [10, 20, 40, 80],
      currentPage: 1, // 当前页码
      pageSize: 10, // 每页的数据条数
      pageCount: "",
      rowCount: 0,
      service: "",
      method: "",
      startTime: "",
      endTime: "",
    };
  },
  methods: {
    clear() {
      this.service = "";
      this.method = "";
      this.startTime = "";
      this.endTime = "";
    },
    search: function () {
      this.currentPage = 1;
      var param = {
        pageIndex: this.currentPage,
        pageSize: this.pageSize,
        service: this.service,
        method: this.method,
        startTime: this.startTime,
        endTime: this.endTime,
      };
      this.request
        .postJson(this.mockapi.queryListByPage, param)
        .then((res) => {
          this.tableData = [];
          this.tableData = res.data;
          this.pageCount = res.pageInfo.pageCount;
          this.rowCount = res.pageInfo.rowCount;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    clickSearch: function () {
      let param = {
        pageIndex: this.currentPage,
        pageSize: this.pageSize,
        service: this.service,
        method: this.method,
        startTime: this.startTime,
        endTime: this.endTime,
      };
      this.request.postJson(this.mockapi.queryListByPage, param).then((res) => {
        this.tableData = [];
        this.tableData = res.data;
        this.pageCount = res.pageInfo.pageCount;
        this.rowCount = res.pageInfo.rowCount;
      });
    },

    logExport() {
      let param = {
        service: this.service,
        method: this.method,
        startTime: this.startTime,
        endTime: this.endTime,
      };
      postDownload(param, this.mockapi.logExport);
    },
    handleSizeChange(val) {
      this.pageSize = val;
      this.clickSearch();
    },
    handleCurrentChange(val) {
      this.currentPage = val;
      this.clickSearch();
    },
  },
  mounted() {
    let Listparam = {
      pageIndex: this.currentPage,
      pageSize: this.pageSize,
      service: this.service,
      method: this.method,
      startTime: this.startTime,
      endTime: this.endTime,
    };
    this.request
      .postJson(this.mockapi.queryListByPage, Listparam)
      .then((res) => {
        this.tableData = res.data;
        this.pageCount = res.pageInfo.pageCount;
        this.rowCount = res.pageInfo.rowCount;
      });
  },
};
</script>
<style src="./interLog.css" scoped></style>



